import React from "react";
import Helmet from 'react-helmet';
import favicon from "../images/favicon.png";
import { webConfig } from "../webConfig";
import { urlJoin } from "../tool";

export interface IHeaderProps 
{
  url: string              //og:url | <canonical link>
  description: string;     //twitter:description | og:description | description
  type: "article" | "website" | "object" //og:type
  title: string;           //twitter:title | og:title
  tags?: string[];          //article:tag
  image?: string;           //og:image | twitter:image
  updated_time?: string;    //og:updated_time | article:modified_time
  publish_time?: string;    //article:publish_time
  section?: string;         //article:section
}

export class Header extends React.Component<{header: IHeaderProps}>
{
  render() 
  {
    let siteUrl = webConfig.domain;
    let prefix  = webConfig.pathPrefix;
    let header  = this.props.header;

    if(header.image != null)
    {
      if(header.image.startsWith(prefix))
        header.image = header.image.substr(prefix.length);
      header.image = urlJoin(siteUrl, header.image);
    }
    if(header.url != null)
    {
      if(header.url.startsWith(prefix))
        header.url = header.url.substr(prefix.length);
      header.url = urlJoin(siteUrl, header.url);
    }

    return (
      <Helmet defer={false}>
        <html lang="DE" />
        <title>{header.title}</title>
        <link rel="icon" type="image/png" sizes="32x32" href={favicon}/>
        <meta name="viewport" content="width=device-width, initial-scale=1"/>
        <meta http-equiv="Content-Type" content="text/html; charset=UTF-8"/>
        {
          header.description != null ? <meta name="description" content={header.description}/> : ""
        }
        <link rel="canonical" href={header.url}></link>
        
        {/* og */}
        <meta property="og:locale" content="de_DE"/>
        <meta property="og:type" content={header.type}/>
        <meta property="og:title" content={header.title}/>
        {
          header.description != null ? <meta property="og:description" content={header.description}/> : ""
        }
        <meta property="og:url" content={header.url}/>
        <meta property="og:site_name" content="M Formatter | Beautify your PowerQuery code"/>
        {
          header.updated_time != null ? <meta property="og:updated_time" content={header.updated_time}/> : ""
        }
        {
          header.image != null ? <meta property="og:image" content={header.image}/> : ""
        }
        {
          header.image != null ? <meta property="og:image:secure_url" content={header.image}/> : ""
        }

        {/* article */}
        <meta property="article:publisher" content={webConfig.domain}/>
        {
          header.tags != null ? header.tags.map((t,i) => <meta key={i} property="article:tag" content={t}/>) : ""
        }
        {
          header.section == "article" ? <meta property="article:section" content={header.section}/> : ""
        }
        {
          header.updated_time != null ? <meta property="article:modified_time" content={header.updated_time}/> : ""
        }
        {
          header.publish_time != null ? <meta property="article:published_time" content={header.publish_time}/> : ""
        }

        {/* twitter */}
        <meta name="twitter:card" content="summary"/>
        {
          header.description != null ? <meta name="twitter:description" content={header.description}/> : ""
        }
        <meta name="twitter:title" content={header.title}/>
        <meta name="twitter:site" content="@ppvinsights"/>
        <meta name="twitter:creator" content="@ppvinsights"/>
        {
          header.image != null ? <meta name="twitter:image" content={header.image}/> : ""
        }
      </Helmet>
    )
  }
}