import Dexie from "dexie";

export class DbService
{
  static db: Dexie;
  public static init()
  {
    this.db = new Dexie("mFormatterDb");
    this.db.version(1).stores({
      std: "options"
    });
  }
  
  public static async storeOptions(options)
  {
    return new Promise((resolve, reject) => {
      this.db.table("std").put({
        options: JSON.stringify(options)
      }).then(() => resolve(), (e) => reject(e));
    });
  }
  
  public static async getOptions()
  {
    return new Promise((resolve, reject) => {
      this.db.table("std").get("options").then(v => resolve(v), (e) => reject(e));
    });
  }
}