import React from "react";
import Logo from "../images/logo.svg";
import { Link } from "gatsby";
import { OutboundLink } from "gatsby-plugin-google-analytics";
import { FaTwitter, FaFacebook } from "react-icons/fa";
import "./Navbar.sass";

interface NavbarState {
  active: boolean;
  navBarActiveClass: string;
}

export class Navbar extends React.Component<{}, NavbarState>
{
  constructor(props)
  {
    super(props);
    this.state = {
      active: false,
      navBarActiveClass: ""
    }
  }

  toggleHamburger = () => {
    // toggle the active boolean in the state
    this.setState(
      {
        active: !this.state.active
      },
      // after state has been updated,
      () => {
        // set the class in state for the navbar accordingly
        this.state.active
          ? this.setState({
              navBarActiveClass: "is-active"
            })
          : this.setState({
              navBarActiveClass: ""
            });
      }
    );
  };

  render()
  {
    let navbarItems = [
      { text: "Home", route: "/"},
      { text: "Api Documentation", route: "/api"},
      { text: "Bug Report", route: "/bug"},
    ]
    return (
      <nav className="navbar is-transparent" role="navigation" aria-label="main-navigation">
        <div className="container">
          <div className="navbar-brand">
            <Link to="/" title="Logo">
              <Logo className="logo" />
            </Link>
            {/* Hamburger menu */}
            <div className={`navbar-burger burger ${this.state.navBarActiveClass}`} data-target="navMenu" onClick={() => this.toggleHamburger()}>
              <span />
              <span />
              <span />
            </div>
          </div>
          <div id="navMenu" className={`navbar-menu ${this.state.navBarActiveClass}`}>
            <div className="navbar-start has-text-centered">
            {
              navbarItems.map((n,i) => {
                return (
                  <Link key={i} className="navbar-item" activeClassName="navbar-active" to={n.route}>
                    <span>{n.text}</span>
                  </Link>
                )
              })
            }
            </div>
            <div className="navbar-end has-text-centered">
              <OutboundLink className="navbar-item social" aria-label="Twitter@ppvinsights" href="https://twitter.com/ppvinsights" rel="noopener nofollow" target="_blank">
                <FaTwitter/>
              </OutboundLink>
              <OutboundLink className="navbar-item" aria-label="Facebook" href="https://www.facebook.com/powerpivotinsights" rel="noopener nofollow" target="_blank">
                <FaFacebook/>
              </OutboundLink>
            </div>
          </div>
        </div>
      </nav>
    )
  }
}