import React from "react";
import "./CookieWarning.sass";
import { Link } from 'gatsby';
import { getCookieObject } from "../tool";

export class CookieWarning extends React.Component<{}, { cookieWarningDismissed: boolean}>
{

  constructor(props = null)
  {
    super(props);
    this.state = {
      cookieWarningDismissed: false
    }
  }
  
  componentDidMount()
  {
    let cookieObj = getCookieObject();
    this.setState({cookieWarningDismissed: cookieObj.cookieWarningDismissed === "true"});
  }

  on_okButton_clicked()
  {
    document.cookie = "cookieWarningDismissed=true"
    this.setState({cookieWarningDismissed: true});
  }

  render()
  {
    if(this.state.cookieWarningDismissed == true)
    {
      return (
        <>
        </>
      )
    }
    else 
    {
      return (
        <div className="cookie-warning-container">
          <div className="cookie-warning">
            <div className="cookie-text">
              Diese Website benutzt Cookies. Wenn du die Website weiter nutzt, gehen wir von deinem Einverständnis aus. 
            </div>
            <div>
              <span>
                <button onClick={this.on_okButton_clicked.bind(this)} className="button">
                  OK
                </button>
              </span>
              <span>
                <Link className="button" to="/privacy">
                  Erfahre mehr
                </Link>
              </span>
            </div>
          </div>
        </div>
      )
    }
  }
}
