import { IConfig } from "./components/Formatter";
import { getCookieObject } from "./tool";
import { DbService } from './misc/dbService';

export var defaultOptions: IConfig = {
  indentation: "&nbsp;&nbsp;",
  indentationLength: 2,
  lineEnd: "<br/>",
  ws: "&nbsp;",
  lineWidth: 100,
  numBrackets: 3,
  escapeHtmlText: true,
  alignPairedExpressionsByEqual: false,
  inlineCssToggle: true,
  inlineCss: `
.constant.keyword {
color: #c586c0;
}

.constant {
color: #d4d4d4;
}

.identifier {
color: #9cdcfe;
}

.operator {
color: #d4d4d4;
}

.bracket {
font-weight: bold;
}

.bracket-0 {
color: Gold;
}

.bracket-1 {
color: GoldenRod;
}

.bracket-2 {
color: DarkGoldenRod;
}

.type {
color: #4ec9b0;
}

.literal.null {
color: #569cd6;
}

.literal.string {
color: #ce9178;
}

.literal {
color: #dcdcaa;
}

body {
font-family: monospace;
background-color: #1e1e1e;
}
`
};

export async function getOptions(): Promise<IConfig>
{
  DbService.init();
  try
  {
    let options = await DbService.getOptions();
    if(options != null)
    {
      console.log("options from db");
      return options;
    }
    else
    {
      console.log("default options");
      return JSON.parse(JSON.stringify(defaultOptions));
    }
  }
  catch(error)
  {
    return JSON.parse(JSON.stringify(defaultOptions));
  }
}
