export function urlJoin(a: string, b: string)
{
  if(a[a.length-1] == "/")
    a = a.substring(0, a.length-1);
  if(b[0] == "/")
    b = b.substring(1);
  return a + "/" + b;
}

export function getCookieObject() : any
{
  let cookieObj = {};
  if(document.cookie)
  {
    document.cookie.split(";")
      .map(c => c.split("="))
      .forEach(c => {
        cookieObj[c[0].trim()] = c[1];
      });
  }
  return cookieObj;
}

export function validateEmail(email) 
{
  return /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(email)
}
