import React from "react";
import { Header, IHeaderProps } from './Header';
import { Navbar } from "./Navbar";
import { Footer } from "./Footer";
import "../all.sass";
import { CookieWarning } from "./CookieWarning";

export class Layout extends React.Component<{header: IHeaderProps, sidebar?: React.Component}>
{
  render()
  {
    return (
      <div>
        <Header header={this.props.header}></Header>
        <Navbar></Navbar>
        <div className="container-all">
          <section className="section">
            <div className="container content" id="content-container">
              <div className="columns">
                <div className="column is-10 is-offset-1">
                  {this.props.children}
                </div>
                <div className="column is-3">
                {
                  this.props.sidebar != null ? this.props.sidebar : ""
                }
                </div>
              </div>
            </div>
          </section>
          <Footer></Footer>
        </div>
        <CookieWarning/>
      </div>
    )
  }
}